@charset "UTF-8";
.container {
  width: 330px;
  padding: 0 15px;
  margin: 0 auto;
}
@media (min-width: 660px) {
  .container {
    width: 580px;
    padding: 0 40px;
  }
}
@media (min-width: 1360px) {
  .container {
    width: 1160px;
    padding: 0 100px;
  }
}

.main-body {
  font-family: "Ubuntu", "Arial", sans-serif;
  font-size: 16px;
  line-height: 19px;
  min-width: 360px;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-header__intro-block {
  background-color: #f0f0f0;
  background-image: url("../img/mobile/bg-hdr.png");
  background-repeat: no-repeat;
  background-position: center 80px;
  margin-top: 84px;
  padding-top: 30px;
  padding-bottom: 300px;
}
@media (min-resolution: 2dppx) {
  .main-header__intro-block {
    background-image: url("../img/mobile/bg-hdr@2x.png");
    background-size: 360px 450px;
  }
}
.main-header__intro-block h1 {
  font-size: 32px;
  line-height: 37px;
  text-align: center;
  width: 275px;
  margin: 0 auto;
}
.main-header__intro-block p {
  position: relative;
  width: 236px;
  margin: 25px auto;
}
.main-header__intro-block p::before {
  content: "";
  position: absolute;
  left: -35px;
  width: 30px;
  height: 30px;
  background: url("../img/mask-icon.svg") no-repeat;
}

@media (min-width: 660px) {
  .main-header__intro-block {
    background-image: url("../img/desktop/bg-hdr.png");
    background-size: 500px 290px;
    background-position: right 200px;
    padding-top: 60px;
    padding-bottom: 255px;
  }
}
@media (min-width: 660px) and (min-resolution: 2dppx) {
  .main-header__intro-block {
    background-image: url("../img/desktop/bg-hdr@2x.png");
    background-size: 500px 290px;
  }
}
@media (min-width: 660px) {
  .main-header__intro-block h1 {
    font-size: 35px;
    line-height: 40px;
    text-align: left;
    width: 475px;
    margin: 0 auto 0 40px;
  }
}
@media (min-width: 660px) {
  .main-header__intro-block p {
    font-size: 19px;
    line-height: 22px;
    width: 350px;
    margin: 25px auto 25px 40px;
  }
  .main-header__intro-block p::before {
    left: 300px;
  }
}
@media (min-width: 1360px) {
  .main-header__intro-block {
    background-size: 700px 400px;
    background-position: right 20px;
    margin-top: 173px;
    padding-top: 80px;
    padding-bottom: 95px;
  }
  .main-header__intro-block h1 {
    font-size: 45px;
    line-height: 52px;
    width: 780px;
  }
  .main-header__intro-block p {
    font-size: 30px;
    line-height: 35px;
    width: 560px;
    margin: 25px auto 25px 100px;
  }
  .main-header__intro-block p::before {
    width: 63px;
    height: 63px;
    background-size: contain;
    top: 5px;
    left: -70px;
  }
}
.main-nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #ffffff;
  z-index: 3;
}
.main-nav--open .main-nav__nav-button span:nth-of-type(1) {
  animation: ease 0.5s topOpen forwards;
}
.main-nav--open .main-nav__nav-button span:nth-of-type(2) {
  animation: ease 0.5s mdlOpen forwards;
}
.main-nav--open .main-nav__nav-button span:nth-of-type(3) {
  animation: ease 0.5s btmOpen forwards;
}
.main-nav--open .main-nav__nav-list {
  transform: translateX(0);
  transition: 0.4s;
}

.main-nav__nav-head {
  display: flex;
  column-gap: 60px;
  width: 330px;
  background-color: #ffffff;
  margin: 0 auto;
  padding: 10px 15px;
}

.main-nav__nav-button {
  align-self: center;
  width: 29px;
  height: 19px;
  background-color: transparent;
  margin: 0;
  padding: 0;
  border: none;
  cursor: pointer;
}
.main-nav__nav-button span {
  position: relative;
  display: block;
  width: 100%;
  height: 3px;
  background-color: #dd001a;
}
.main-nav__nav-button span:nth-of-type(1) {
  animation: ease 0.5s topClose forwards;
}
.main-nav__nav-button span:nth-of-type(2) {
  animation: ease 0.5s mdlClose forwards;
}
.main-nav__nav-button span:nth-of-type(3) {
  animation: ease 0.5s btmClose forwards;
}
.main-nav__nav-button span + span {
  margin-top: 7px;
}

.main-nav__form-search,
.main-nav__profile,
.main-nav__favorites {
  display: none;
}

.main-nav__logo {
  display: block;
  width: 147px;
  height: 64px;
  background: url("../img/logo.png") no-repeat;
}
@media (min-resolution: 2dppx) {
  .main-nav__logo {
    background: url("../img/logo@2x.png") no-repeat;
    background-size: 147px 64px;
  }
}

.main-nav__nav-block {
  display: flex;
}

.main-nav__shop {
  align-self: center;
  display: block;
  width: 30px;
  height: 30px;
  background: url("../img/mobile/shop.svg") no-repeat;
}

.main-nav__nav-list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  position: absolute;
  top: 84px;
  left: 0;
  right: 0;
  background-color: #ffffff;
  transform: translateX(-100%);
  list-style: none;
  margin: 0;
  padding: 20px 15px;
  transition: 0.4s;
}

.main-nav__nav-item:not(:first-of-type) {
  margin-top: 15px;
}
.main-nav__nav-item a {
  letter-spacing: 1px;
  text-decoration: none;
  color: #000000;
  transition: 0.4s;
}
.main-nav__nav-item a:hover, .main-nav__nav-item a:focus {
  color: #dd001a;
  transition: 0.4s;
}

@media (min-width: 660px) {
  .main-nav__nav-head {
    justify-content: space-between;
    column-gap: 0;
    width: 580px;
    padding: 10px 40px;
  }
}
@media (min-width: 1360px) {
  .main-nav__nav-head {
    width: 1160px;
    padding: 30px 80px 0 80px;
  }

  .main-nav__nav-button {
    display: none;
  }

  .main-nav__nav-list {
    position: static;
    transform: translateX(0);
    flex-direction: row;
    column-gap: 50px;
    width: 1020px;
    margin: 0 auto;
    padding: 25px 0;
  }

  .main-nav__nav-item + .main-nav__nav-item {
    margin-top: 0;
  }

  .main-nav__form-search {
    display: block;
    align-self: center;
  }
  .main-nav__form-search fieldset {
    display: flex;
    border: none;
    margin: 0;
    padding: 0;
  }

  .main-nav__search {
    width: 459px;
    height: 41px;
    padding: 11px 11px 11px 21px;
    border: 1px solid #000000;
  }
  .main-nav__search::placeholder {
    color: #000000;
  }

  .main-nav__search-button {
    font-weight: 700;
    width: 116px;
    height: 41px;
    background-color: #000000;
    color: #ffffff;
    border: none;
    cursor: pointer;
    transition: 0.4s;
  }
  .main-nav__search-button:hover, .main-nav__search-button:focus {
    opacity: 0.7;
    transition: 0.4s;
  }

  .main-nav__nav-block {
    width: 151px;
    align-items: center;
    justify-content: space-between;
  }
  .main-nav__nav-block a {
    transition: 0.4s;
  }
  .main-nav__nav-block a:hover, .main-nav__nav-block a:focus {
    opacity: 0.7;
    transition: 0.4s;
  }

  .main-nav__profile {
    display: block;
    width: 24px;
    height: 26px;
    background: url("../img/desktop/profile.svg") no-repeat;
  }

  .main-nav__favorites {
    display: block;
    width: 28px;
    height: 26px;
    background: url("../img/desktop/favorites.svg") no-repeat;
  }

  .main-nav__shop {
    background: url("../img/desktop/shop.svg") no-repeat;
  }
}
.categories {
  padding-top: 34px;
}
.categories h2 {
  text-align: center;
  margin: 0;
}

.categories__main-block {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}

.categories__block {
  display: block;
  width: 330px;
  height: 160px;
  opacity: 0.8;
  transition: 0.4s;
}
.categories__block--first {
  background: url("../img/mobile/category-1.png") no-repeat;
}
@media (min-resolution: 2dppx) {
  .categories__block--first {
    background: url("../img/mobile/category-1@2x.png") no-repeat;
    background-size: 330px 160px;
  }
}
.categories__block--second {
  margin-top: 12px;
  background: url("../img/mobile/category-2.png") no-repeat;
}
@media (min-resolution: 2dppx) {
  .categories__block--second {
    background: url("../img/mobile/category-2@2x.png") no-repeat;
    background-size: 330px 160px;
  }
}
.categories__block:hover, .categories__block:focus {
  transform: scale(1.05);
  opacity: 1;
  transition: 0.4s;
}

@media (min-width: 660px) {
  .categories {
    padding-top: 50px;
  }

  .categories__main-block {
    flex-direction: row;
    column-gap: 12px;
  }

  .categories__block {
    width: 290px;
    height: 140px;
    background-size: contain;
  }
  .categories__block--second {
    margin-top: 0;
  }
}
@media (min-width: 1360px) {
  .categories h2 {
    font-size: 30px;
    line-height: 35px;
  }

  .categories__main-block {
    column-gap: 0;
    justify-content: space-between;
    margin-top: 24px;
  }

  .categories__block {
    width: 557px;
    height: 270px;
  }
}
.bestsellers {
  padding-top: 40px;
}
.bestsellers h2 {
  text-align: center;
  margin: 0;
}

.bestsellers__product-list {
  display: grid;
  grid-template-columns: 160px 160px;
  grid-template-rows: repeat(4, 295px);
  column-gap: 10px;
  row-gap: 25px;
  list-style: none;
  margin: 0;
  padding: 25px 0 0 0;
}

.bestsellers__product-item {
  display: flex;
  flex-direction: column;
}
.bestsellers__product-item p {
  font-size: 12px;
  line-height: 14px;
  font-weight: 500;
}
.bestsellers__product-item p:first-of-type {
  margin-top: 5px;
  margin-bottom: 10px;
}
.bestsellers__product-item p:last-of-type {
  position: relative;
  margin: 0;
}
.bestsellers__product-item p:last-of-type::before {
  content: "";
  position: absolute;
  bottom: -25px;
  left: 0;
  width: 124px;
  height: 16px;
  background: url("../img/mobile/product-stars.svg") no-repeat;
}

.bestsellers__dynamic-size span:last-of-type,
.bestsellers__dynamic-price span:last-of-type {
  display: none;
}

.bestsellers__buy-button {
  text-transform: uppercase;
  display: block;
  height: 35px;
  background-color: #22222f;
  color: #ffffff;
  margin-top: 38px;
  padding: 0;
  border: none;
  cursor: pointer;
  transition: 0.4s;
}
.bestsellers__buy-button:hover, .bestsellers__buy-button:focus {
  opacity: 0.6;
  transition: 0.4s;
}

@media (min-width: 660px) {
  .bestsellers {
    padding-top: 50px;
  }

  .bestsellers__product-list {
    grid-template-columns: 280px 280px;
    grid-template-rows: 320px 320px;
    column-gap: 18px;
    row-gap: 30px;
    padding-top: 29px;
  }

  .bestsellers__product-item--XL p:first-of-type, .bestsellers__product-item--M p:first-of-type {
    width: 180px;
  }
  .bestsellers__product-item--XL p:last-of-type, .bestsellers__product-item--M p:last-of-type {
    text-align: right;
    margin-right: 40px;
  }
  .bestsellers__product-item--XL p:last-of-type::before, .bestsellers__product-item--M p:last-of-type::before {
    bottom: -40px;
  }
  .bestsellers__product-item--XL p:last-of-type::after, .bestsellers__product-item--M p:last-of-type::after {
    content: "2900 Р";
    font-size: 15px;
    text-decoration: line-through;
    font-weight: 400;
    width: 51px;
    color: #575757;
    position: absolute;
    top: -25px;
    right: 5px;
  }
  .bestsellers__product-item--S p::before, .bestsellers__product-item--XS p::before {
    display: none;
  }
  .bestsellers__product-item--S p:first-of-type, .bestsellers__product-item--XS p:first-of-type {
    margin-bottom: 75px;
  }
  .bestsellers__product-item--S p:last-of-type, .bestsellers__product-item--XS p:last-of-type {
    width: 80px;
    margin-bottom: -45px;
  }
  .bestsellers__product-item--S p:last-of-type::after, .bestsellers__product-item--XS p:last-of-type::after {
    content: "2900 Р";
    font-size: 15px;
    text-decoration: line-through;
    font-weight: 400;
    width: 51px;
    color: #575757;
    position: absolute;
    bottom: 0;
    right: -40px;
  }
  .bestsellers__product-item--S .bestsellers__buy-button, .bestsellers__product-item--XS .bestsellers__buy-button {
    margin-left: 0;
    align-self: flex-end;
  }
  .bestsellers__product-item:nth-of-type(5), .bestsellers__product-item:nth-of-type(6), .bestsellers__product-item:nth-of-type(7), .bestsellers__product-item:nth-of-type(8) {
    display: none;
  }
  .bestsellers__product-item img {
    width: 100%;
  }
  .bestsellers__product-item p {
    font-size: 17px;
    line-height: 21px;
  }
  .bestsellers__product-item p:first-of-type {
    margin-top: 10px;
  }
  .bestsellers__product-item p:last-of-type {
    font-size: 20px;
    line-height: 22px;
  }

  .bestsellers__dynamic-size span:first-of-type,
.bestsellers__dynamic-price span:first-of-type {
    display: none;
  }
  .bestsellers__dynamic-size span:last-of-type,
.bestsellers__dynamic-price span:last-of-type {
    display: inline;
  }

  .bestsellers__buy-button {
    width: 140px;
    height: 35px;
    background-color: #e80a1f;
    margin: 15px 0 0 auto;
  }
}
@media (min-width: 1360px) {
  .bestsellers h2 {
    font-size: 30px;
    line-height: 35px;
  }

  .bestsellers__product-list {
    grid-template-columns: 550px 550px;
    grid-template-rows: 495px 445px;
    justify-content: space-between;
    row-gap: 45px;
  }

  .bestsellers__product-item--XL p:first-of-type, .bestsellers__product-item--M p:first-of-type {
    font-size: 26px;
    line-height: 30px;
    width: 278px;
    margin-left: 40px;
  }
  .bestsellers__product-item--XL p:last-of-type, .bestsellers__product-item--M p:last-of-type {
    font-size: 26px;
    line-height: 30px;
    margin-right: 80px;
  }
  .bestsellers__product-item--XL p:last-of-type::before, .bestsellers__product-item--M p:last-of-type::before {
    bottom: -45px;
    left: 35px;
    width: 176px;
    height: 21px;
    background-size: 176px 21px;
  }
  .bestsellers__product-item--XL p:last-of-type::after, .bestsellers__product-item--M p:last-of-type::after {
    font-size: 18px;
    width: 56px;
    right: 20px;
  }
  .bestsellers__product-item--S p:first-of-type, .bestsellers__product-item--XS p:first-of-type {
    font-size: 26px;
    line-height: 30px;
    width: 490px;
  }
  .bestsellers__product-item--S p:last-of-type, .bestsellers__product-item--XS p:last-of-type {
    font-size: 26px;
    line-height: 30px;
    width: 82px;
  }
  .bestsellers__product-item--S p:last-of-type::after, .bestsellers__product-item--XS p:last-of-type::after {
    font-size: 18px;
    width: 56px;
    right: -65px;
  }
  .bestsellers__product-item img {
    height: 257px;
  }

  .bestsellers__buy-button {
    width: 170px;
    height: 51px;
    margin: 10px 40px 0 auto;
  }
}
.map {
  display: flex;
  flex-direction: column;
  background-color: #f5f5f5;
  min-width: 360px;
  margin-top: 45px;
}
.map img {
  display: block;
  object-fit: cover;
  margin: 0 auto;
}

.map__contacts {
  background-color: #f5f5f5;
  margin-top: -4px;
}

.map__contacts-info {
  display: flex;
  flex-direction: column;
  width: 330px;
  margin: 0 auto;
  padding: 42px 15px 38px 15px;
}
.map__contacts-info h3 {
  font-size: 36px;
  line-height: 42px;
  text-transform: uppercase;
  margin: 0;
}
.map__contacts-info p:first-of-type {
  font-size: 16px;
  line-height: 19px;
  font-weight: 700;
  text-transform: uppercase;
  margin-top: 30px;
  margin-bottom: 25px;
}
.map__contacts-info p:nth-of-type(2) {
  margin: 0;
}

.map__links-block {
  display: flex;
  flex-direction: column;
  row-gap: 22px;
  margin-top: 50px;
  margin-bottom: 33px;
  padding-left: 43px;
}
.map__links-block a {
  font-weight: 700;
  text-decoration: none;
  position: relative;
  color: #000000;
  transition: 0.4s;
}
.map__links-block a:hover, .map__links-block a:focus {
  opacity: 0.8;
  transition: 0.4s;
}

.map__link-phone::before {
  content: "";
  position: absolute;
  top: -3px;
  left: -43px;
  width: 25px;
  height: 25px;
  background: url("../img/phone.svg") no-repeat;
}

.map__link-mail::before {
  content: "";
  position: absolute;
  top: -3px;
  left: -43px;
  width: 25px;
  height: 25px;
  background: url("../img/mail.svg") no-repeat;
}

.map__feedback-button {
  letter-spacing: 1px;
  width: 251px;
  height: 51px;
  background-color: #e80a1f;
  color: #ffffff;
  padding: 0;
  border: none;
  cursor: pointer;
  transition: 0.4s;
}
.map__feedback-button:hover, .map__feedback-button:focus {
  opacity: 0.6;
  transition: 0.4s;
}

.map__decoration--underline {
  text-decoration: underline;
}

@media (min-width: 660px) {
  .map {
    margin-top: 60px;
  }
  .map img {
    width: 660px;
    height: 320px;
  }

  .map__contacts-info {
    width: 580px;
    padding: 42px 40px 38px 40px;
  }
}
@media (min-width: 1360px) {
  .map {
    flex-direction: row;
  }
  .map img {
    width: 795px;
    height: 409px;
  }

  .map__contacts {
    order: -1;
    margin-top: 0;
  }

  .map__contacts-info {
    width: 400px;
    padding-left: 130px;
  }
}
.main-footer h4 {
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 26px;
}
.main-footer a {
  text-decoration: none;
  transition: 0.4s;
}
.main-footer a:not(.main-footer__phone-info, .main-footer__site-link) {
  color: #424242;
}
.main-footer a + a:not(.main-footer__buy-system a) {
  margin-top: 17px;
}
.main-footer a:hover, .main-footer a:focus {
  opacity: 0.6;
  transition: 0.4s;
}
.main-footer p {
  color: #424242;
  margin: 0;
}

.main-footer__phone-info {
  color: #000000;
  margin-top: 17px;
}

.main-footer__map-of-site {
  background-color: #efefef;
  padding: 23px 15px 30px 15px;
}

.main-footer__our-sites {
  display: flex;
  flex-direction: column;
  width: 330px;
  margin: 0 auto;
}

.main-footer__site-link {
  font-weight: 500;
  color: #e80a1f;
  display: flex;
  justify-content: center;
  align-items: center;
