}
.main-footer__site-link:hover, .main-footer__site-link:focus {
  opacity: 0.6;
  transition: 0.4s;
}

.main-footer__footer-logo {
  display: block;
  width: 147px;
  height: 64px;
  background: url("../img/logo.png") no-repeat;
  margin-bottom: 18px;
}
@media (min-resolution: 2dppx) {
  .main-footer__footer-logo {
    background: url("../img/logo@2x.png") no-repeat;
    background-size: 147px 64px;
  }
}

.main-footer__contacts {
  display: flex;
  flex-direction: column;
  width: 330px;
  margin: 0 auto;
  padding-top: 40px;
}
.main-footer__contacts p:last-of-type {
  margin-top: 17px;
}

.main-footer__visitors {
  display: flex;
  flex-direction: column;
  width: 330px;
  margin: 0 auto;
  padding-top: 80px;
}

.main-footer__clients {
  display: flex;
  flex-direction: column;
  width: 330px;
  margin: 0 auto;
  padding-top: 40px;
}

.main-footer__other {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 330px;
  row-gap: 27px;
  margin: 0 auto;
