.main-body {
  font-family: "Ubuntu", "Arial", sans-serif;
  font-size: 16px;
  line-height: 19px;
  min-width: 360px;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
